<template>
    <div class="dashboard">
        <div id="cpu"></div>
        <div id="mem"></div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            cpu: 0,
            MenPercent: 0
        };
    },
    methods: {
        createCpu() {
            // 基于准备好的dom，初始化echarts实例
            var myChart = this.$echarts.init(document.getElementById("cpu"));
            myChart.showLoading();
            window.onresize = myChart.resize;
            // 指定图表的配置项和数据
            let data = [];
            let max = Date.now() + 18000;
            let min = Date.now() + 1000;
            var option = {
                title: {
                    text: "cpu % 利用率"
                },
                tooltip: {
                    trigger: "axis",
                    formatter: function(params) {
                        // 鼠标经过显示的内容
                        params = params[0];
                        return "% 利用率: " + params.data[1].toFixed(2);
                    },
                    axisPointer: {
                        animation: false
                    }
                },
                xAxis: {
                    type: "time",
                    interval: 6000,
                    max: max,
                    min,
                    splitLine: {
                        show: true
                    }
                },
                yAxis: {
                    type: "value",
                    max: 100,
                    splitLine: {
                        show: true
                    }
                },
                series: [
                    {
                        name: "% 利用率",
                        type: "line",
                        showSymbol: false,
                        hoverAnimation: false,
                        data
                    }
                ]
            };
            let xtimes = 0;
            let date = Date.now();
            // 使用刚指定的配置项和数据显示图表。
            setInterval(() => {
                xtimes++;
                if (xtimes > 18) {
                    option.xAxis.max = option.xAxis.max + 1000;
                    option.xAxis.min = option.xAxis.min + 1000;
                    // data.splice(0, 1);
                }

                myChart.hideLoading();
                data.push([Date.now(), this.cpu]);
                myChart.setOption(option);
            }, 1000);
        },
        createMem() {
            // 基于准备好的dom，初始化echarts实例
            var myChart = this.$echarts.init(document.getElementById("mem"));
            window.addEventListener("resize", () => {
                myChart.resize();
            });
            myChart.showLoading();

            // 指定图表的配置项和数据
            let data = [];
            let max = Date.now() + 18000;
            let min = Date.now() + 1000;
            var option = {
                title: {
                    text: "内存 % 利用率"
                },
                tooltip: {
                    trigger: "axis",
                    formatter: function(params) {
                        // 鼠标经过显示的内容
                        params = params[0];
                        return "% 利用率: " + params.data[1];
                    },
                    axisPointer: {
                        animation: false
                    }
                },
                xAxis: {
                    type: "time",
                    interval: 6000,
                    max: max,
                    min,
                    splitLine: {
                        show: true
                    }
                },
                yAxis: {
                    type: "value",
                    max: 100,
                    splitLine: {
                        show: true
                    }
                },
                series: [
                    {
                        name: "% 利用率",
                        type: "line",
                        showSymbol: false,
                        hoverAnimation: false,
                        data
                    }
                ]
            };
            let xtimes = 0;
            let date = Date.now();
            // 使用刚指定的配置项和数据显示图表。
            setInterval(() => {
                xtimes++;
                if (xtimes > 18) {
                    option.xAxis.max = option.xAxis.max + 1000;
                    option.xAxis.min = option.xAxis.min + 1000;
                    // data.splice(0, 1);
                }
                myChart.hideLoading();
                data.push([Date.now(), this.MenPercent]);
                myChart.setOption(option);
            }, 1000);
        },
        addListenSocket() {
            if (!this.$socket.id) {
                let timeOut = setTimeout(() => {
                    this.addListenSocket();
                    clearTimeout(timeOut);
                }, 500);
                return;
            }
            this.$socket.emit("cpu", { id: this.$socket.id });
            this.$socket.on(this.$socket.id + "_cpu", msg => {
                this.cpu = msg.CpuPercent;
                this.MenPercent = msg.MenPercent;
            });
        },
        removeListent() {
            this.$socket.emit(this.$socket.id + "_cpu_remove", {
                id: this.$socket.id + "_cpu"
            });
        }
    },
    mounted() {
        this.createCpu();
        this.createMem();
    },
    activated() {
        this.addListenSocket();
    },
    deactivated() {
        this.removeListent();
    }
};
</script>

<style lang="scss" scoped>
#cpu,
#mem {
    min-height: 400px;
}
</style>